.gridContainer {
  display: grid;
  grid-template-areas:
    "mname mname empty empty"
    "mname mname namecate rate"
    "link dist loc loc";
  grid-gap: 0px;
  background-color: #fff;
  padding-top: 10px;
  padding-bottom: 10px;
  margin: 5px;
}

.mname {
  grid-area: mname;
  background-color: #fe7f2d;
  padding: 20px;
  border-radius: 10px 10px 0px 0px / 10px 10px 0px 0px;
}
.empty {
  grid-area: empty;
  height: 20px;
}
.namecate {
  grid-area: namecate;
  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #f8c5a4;
}
.rate {
  grid-area: rate;
  background-color: #fff;
  padding: 10px;
  background-color: #f8c5a4;
}
.link {
  grid-area: link;
  font-size: 12px;
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 5px;
  background-color: #f8c5a4;
  border-bottom: 3px solid rgb(73, 72, 72);
}
.loc {
  grid-area: loc;
  font-size: 12px;
  padding: 5px;
  background-color: #f8c5a4;
  border-bottom: 3px solid rgb(73, 72, 72);
  /* border-right: 1px solid black; */
}
.dist {
  grid-area: dist;
  font-size: 12px;
  padding: 5px;
  background-color: #f8c5a4;
  border-bottom: 3px solid rgb(73, 72, 72);
}

a {
  text-decoration: none;
}

a:active {
  text-decoration: none;
  color: black;
}

a:link {
  text-decoration: none;
  color: black;
}

a:visited {
  text-decoration: none;
  color: black;
}
